#experience
{
    background-color: #9b59d0;
    width: 100vw;
    height: 100vh;
    color: #fcf434;
    text-shadow: 1px 1px 3px #fffb89
}

#experience main
{
    height: 100vh;
}

.skill-list
{
    display: grid;
    grid-template-columns: 15vw 1fr;
    padding: 30px 0;
    overflow-x: hidden;
    max-height: 100vh;
}

.skill-list h2
{
    font-size: 25px;
    align-self: center;
    margin: 0;
    text-align: right;
    padding-right: 20px;
}

.skill-list ul
{
    display: grid;
    grid-template-columns: repeat(auto-fill, 100px);
    grid-template-rows: repeat(auto-fill, 100px);
    column-gap: 2px;
    row-gap: 7px;
    border-left: 1px solid #fcf434;
    padding-left: 10px;
}

#soft-skills ul
{
    grid-template-columns: repeat(auto-fill, 150px);
}

ul
{
    list-style: none;
    margin: 0;
    padding: 0;
    display: flex;
}

.advanced-full
{
    background-color: #009b08;
    border: 2px solid #1cd826;
    padding: 5px 10px;
    color: white;
    margin-right: 10px;
}

.intermediate-full
{
    background-color: #a76c00;
    border: 2px solid #df960e;
    padding: 5px 10px;
    color: white;
    margin-right: 10px;
}

.beginner-full
{
    background-color: #9b1200;
    border: 2px solid #d62710;
    padding: 5px 10px;
    color: white;
}

.advanced
{
    border-bottom: 5px solid #1cd826;
}

.intermediate
{
    border-bottom: 5px solid #df960e;
}

.beginner
{
    border-bottom: 5px solid #d62710;
}

.inner-slide
{
    width: 100%;
}

#awards,
.job-list,
.school-list
{
    width: 100%;
    height: 70vh;
    padding: 0 3vh;
    padding-bottom: none;
    overflow-y: scroll;
    display: flex;
    flex-direction: column;
    scrollbar-gutter: stable;
    margin-top: 5vh;
}

#awards::-webkit-scrollbar,
.job-list::-webkit-scrollbar,
.school-list::-webkit-scrollbar
{
    width: 10px;
    background-color: transparent;
    border-radius: 20px;
    height: 80%;
}

#awards::-webkit-scrollbar-thumb,
.job-list::-webkit-scrollbar,
.shool-list::-webkit-scrollbar
{
    background-color:#fcf434;
    border-radius: 100px;
    width: 10px;
    height: 10px;
    box-shadow: inset 0 0 5px 0 #918d1b;
}

.award:last-child
{
    padding-bottom: 10vh;
}

.award:first-child
{
    padding-top: 10vh;
}

.job-list,
.school-list
{
    display: grid;
    grid-template-rows: repeat(auto-fill, 25vh);
    row-gap: 5vh;
    overflow-y: auto;
}

.job-list
{
    grid-template-rows: repeat(auto-fill, 30vh);
}

@media only screen and (max-width: 1024px)
{
    #experience .slide
    {
        padding: 0 2.5vw;
    }
}

@media only screen and (max-width: 768px)
{
    #experience .skill-list h2
    {
        font-size: 20px;
    }

    #experience .skill-list ul
    {
        grid-template-columns: repeat(auto-fill, 70px);
        grid-template-rows: repeat(auto-fill, 70px);
    }

    #experience #soft-skills ul
    {
        grid-template-columns: repeat(auto-fill, 100px);
        grid-template-rows: repeat(auto-fill, 70px);
    }

    #experience .skill p
    {
        font-size: 13px;
    }

    #awards
    {
        padding-left: 0;
        padding-right: 1.5vw;
    }

    .job-list,
    .school-list
    {
        padding-left: 0;
        padding-right: 1.5vw;
        grid-template-rows: repeat(auto-fill, 25vh);
    }
}

@media only screen and (max-width: 425px)
{
    .school-list
    {
        row-gap: 1vh;
    }

    #experience .skill-list h2
    {
        font-size: 15px;
    }

    #experience .skill-list ul
    {
        grid-template-columns: repeat(auto-fill, 50px);
        grid-template-rows: repeat(auto-fill, 50px);
    }

    #experience #soft-skills ul
    {
        grid-template-columns: repeat(auto-fill, 65px);
        grid-template-rows: repeat(auto-fill, 50px);
    }

    #experience .skill p
    {
        font-size: 9px;
    }

    #experience .skill-overlay
    {
        font-size: 10px;
    }

    .skill-list
    {
        grid-template-columns: 25vw 1fr;
        padding: 10px 0;
    }

    .advanced-full,
    .intermediate-full,
    .beginner-full
    {   
        font-size: 15px;
    }

    #levels
    {
        padding: 10px 0;
    }
}