.project
{
    position: relative;
    border-radius: 10%;
    cursor: pointer;
    transition: transform 0.25s;
}

.project:hover
{
    transform: scale(1.05, 1.05);
}

.project-img,
.project-img img
{
    width: 100%;
    height: 100%;
    position: absolute;
    left: 0;
    top: 0;
    border-radius: 10%;
}

.project-overlay
{
    z-index: 1;
    background-color: #00000099;
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    border-radius: 10%;
    color: white;
    padding: 20px 10px;
    box-sizing: border-box;
    opacity: 0;
    transition: opacity 0.25s;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.project-overlay *
{
    margin: 0;
}

.project:hover .project-overlay
{
    opacity: 1;
}

.project-overlay h3
{
    font-size: 20px;
}

.project-overlay p
{
    font-size: 15px;
}

@media only screen and (max-width: 768px)
{
    .project-overlay h3
    {
        font-size: 15px;
    }

    .project-overlay p
    {
        font-size: 10px;
    }
}

@media only screen and (max-width: 425px)
{
    .project-overlay
    {
        padding: 10px 5px;
    }

    .project-overlay h3
    {
        font-size: 10px;
    }

    .project-overlay p
    {
        font-size: 9px;
    }
}